
.slogan {
  height: 2.5rem;
  width: 100%;
  text-align: center;
  clear: both;
  background: white;
}
.slogan1 {
  width: 60%;
  height: 1.25rem;
  margin: auto;
  border-bottom: 1px solid #bababa;
  margin-bottom: -0.725rem;
}
.slogan2 span {
  height: 1.25rem;
  line-height: 1.25rem;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  background: white;
}
.banner img {
  width: 100%;
  max-width: 100%;
  max-height: 100%;
}
.gold_course {
  box-sizing: border-box;
  width: 100%;
  padding: 0.5rem 0 0.5rem 0;
  background: #f4f8fb;
}
.gold_course li {
  width: 23.5%;
  margin: 0 auto;
  display: inline-block;
  text-align: center;
}
.gold_course li a img {
  width: 80%;
}
.gold_course li a {
  display: inline-block;
}
.gold_course li span {
  display: block;
  font-size: 75%;
}
.large_items {
  width: 100%;
  background: #f4f8fb;
  padding: 0.5rem;
  margin: 0 auto;
}
.large_items ul {
  width: 85%;
  margin: 0 auto;
}
.large_items ul li {
  width: 32%;
  display: inline-block;
}
.large_items ul li div {
  width: 100%;
  padding-top: 0.5rem;
  display: inline-block;
}
.large_items ul li div:nth-of-type(2) span {
  text-align: center;
  font-size: 75%;
  display: block;
  margin-left: -40%;
}
.large_items ul li div:nth-of-type(1) img {
  width: 43%;
  max-width: 43%;
  margin: 0 auto;
  text-align: center;
  display: inline-block;
  vertical-align: middle;
}
.large_items ul li div:nth-of-type(1) img:nth-of-type(1) {
  padding-left: 8%;
}
.brief {
  background-color: #f2f8ff;
}
.brief p {
  width: 95%;
  margin: auto;
  text-indent: 2rem;
}
.teacher_icon ul li {
  float: left;
  width: 25%;
  text-align: center;
  margin-bottom: 0.75rem;
}
.icon {
  width: 4rem;
  height: 4rem;
  margin: 0.8rem auto;
  border-radius: 100%;
  border: 2px solid #5f5f5f;
  padding: 3px;
  overflow: hidden;
}
.icon img {
  width: 100%;
  height: 100%;
}
.pyramid {
  display: block;
  background: url(../assets/pyramid.jpg) no-repeat 20% center;
  background-size: 12rem 9.375rem;
  clear: both;
}




