
.nav {
  width: 100%;
  height: 2.5rem;
  line-height: 2.5rem;
  float: left;
  background-color: #EDEDED;
  padding-bottom: 3px;
}
.nav li {
  width: 20%;
  text-align: center;
  float: left;
}
.nav li a {
  height: 100%;
  display: block;
  text-decoration: none;
  color: black;
}
.nav li a:hover {
  border-bottom: 3px solid #248cf3;
  color: #248cf3;
}
.nav ul li router-link-active {
  border-bottom: 3px solid #248cf3;
  color: #248cf3;
}




