
.header {
  height: 3rem;
  background-color: #1768bb;
  color: white;
  box-sizing: border-box;
  padding: 0.75rem;
}
.header img {
  width: 1.5rem;
  height: 1.5rem;
  vertical-align: top;
}
.xkjy {
  height: 100%;
  float: left;
  margin-left: 10px;
}
.logo {
  height: 100%;
  display: inline-block;
  float: left;
  width: 1.5rem;
}
.logo_home {
  height: 100%;
  display: inline-block;
  width: 1.5rem;
  float: right;
}




